const selectedChars = document.getElementById("selected-chars");
const inputForm = document.getElementById("input-form");
const userInput = document.getElementById("user-input");
const resultTxt = document.getElementById("result");
const wordScoreTxt = document.getElementById("wordscore");
const scoreTxt = document.getElementById("score-txt");
const timerTxt = document.getElementById("timer-txt");
let SCORE = 0;
let WILDCARDS = [];
const userWords = [];
let timeRemaining = 120;
//prettier-ignore
const consonants = ['B', 'C', 'D', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W'];
const vowels = ["A", "E", "I", "O", "U"];

function generateLetters() {
	let letters = [];

	letters.push(vowels[Math.floor(Math.random() * vowels.length)]);

	while (letters.length < 5) {
		const consonant = consonants[Math.floor(Math.random() * consonants.length)];
		if (!letters.includes(consonant)) {
			letters.push(consonant);
		}
	}

	return letters;
}
const validChars = generateLetters();
//selectedChars.textContent = validChars.join(" ");
validChars.forEach((e, i) => {
	const charSpan = document.createElement("span");
	charSpan.textContent = e;
	charSpan.style.margin = "5px";
	charSpan.setAttribute("id", `l${i}`);
	selectedChars.appendChild(charSpan);
});

async function getWord(word) {
	const response = await fetch(
		`https://api.dictionaryapi.dev/api/v2/entries/en/${word}`
	);
	const data = await response.json();

	return data;
}

userInput.addEventListener("input", (e) => {
	[...document.getElementsByTagName("span")].forEach((e) => {
		e.style.backgroundColor = "white";
		e.style.color = "black";
	});
	WILDCARDS = [];
	userInput.value.split("").forEach((e) => {
		if (validChars.indexOf(e.toUpperCase()) < 0) {
			WILDCARDS.push(e);
		} else {
			let currentChar = document.getElementById(
				`l${validChars.indexOf(e.toUpperCase())}`
			);
			currentChar.style.backgroundColor = "green";
			currentChar.style.color = "white";
		}
	});
});

inputForm.addEventListener("submit", async (e) => {
	e.preventDefault();
	const userWord = userInput.value;

	if (userWords.length === 0) {
		const timer = setInterval(() => {
			timeRemaining--;
			timerTxt.textContent = `Time: ${Math.floor(timeRemaining / 60)}:${(
				timeRemaining % 60
			)
				.toString()
				.padStart(2, "0")}`;
			if (timeRemaining === 0) {
				clearInterval(timer);
				userInput.disabled = true;
			}
		}, 1000);
	}

	try {
		const result = await getWord(userWord);

		if (WILDCARDS.length >= 2 || result.title || userWords.includes(userWord)) {
			resultTxt.textContent = `INVALID word: "${userWord}"`;
			resultTxt.classList.remove("green");
			resultTxt.classList.add("red");
			wordScoreTxt.textContent =
				WILDCARDS.length >= 2
					? `You used more than 1 wildcard letter: ${WILDCARDS.join(" ")}`
					: userWords.includes(userWord)
					? `You have already entered the word "${userWord}"`
					: `The word "${userWord}" was not found in the dictionary`;
		} else {
			resultTxt.textContent = `VALID word: "${userWord}"`;
			resultTxt.classList.remove("red");
			resultTxt.classList.add("green");
			userWords.push(userWord);
			wordScoreTxt.textContent = getScore(userWord.length, WILDCARDS.length);
			scoreTxt.textContent = `Score: ${SCORE}`;
		}
		WILDCARDS = [];
		userInput.value = "";
		[...document.getElementsByTagName("span")].forEach((e) => {
			e.style.backgroundColor = "white";
			e.style.color = "black";
		});
	} catch (error) {
		resultTxt.classList.remove("green");
		resultTxt.classList.add("red");
		resultTxt.textContent = "Cannot reach dictionary API";
		wordScoreTxt.textContent = error.message;
	}
});
//prettier-ignore
function getScore(wordlength, wildcard) {
  if (wordlength === 1) {
    SCORE += wildcard ? 0 : 10
    return wildcard ? `One letter words using wildcard letter only give no score` : `One letter word, no wildcard. 10 points`
  } else if (wordlength === 2) {
    SCORE += wildcard ? 20 : 50
    return wildcard ? `Two letter word, using wildcard. 20 points.` : `Two letter word, no wildcard. 50 points`
  } else if (wordlength === 3) {
    SCORE += wildcard ? 100 : 150
    return wildcard ? `Three letter word, using wildcard. 100 points.` : `Three letter word, no wildcard. 150 points`
  } else if (wordlength === 4) {
    SCORE += wildcard ? 200 : 300
    return wildcard ? `Four letter word, using wildcard. 200 points.` : `Four letter word, no wildcard. 300 points`
  } else if (wordlength === 5) {
    SCORE += wildcard ? 350 : 500
    return wildcard ? `Five letter word, using wildcard. 350 points. Good!` : `Five letter word, no wildcard. 500 points. Nice!`
  } else {
    SCORE += 600
    return `6 letter word, using 1 wildcard. 600 points, max score, well done!`
  }
}
